import {Component, OnInit} from '@angular/core';
import {SermonService} from "../../services/sermon.service";
import {Storage} from "@ionic/storage";
import {ActivatedRoute} from "@angular/router";
import {IdService} from "../../services/id.service";
import loader from "@angular-devkit/build-angular/src/angular-cli-files/plugins/single-test-transform";
import {LoaderService} from "../../services/loader.service";


@Component({
    selector: 'app-ism-home',
    templateUrl: './ism-home.page.html',
    styleUrls: ['./ism-home.page.scss'],
})
export class IsmHomePage implements OnInit {
    slideOpts = {
        slidesPerView: 1.1,
        centeredSlides: true,
        spaceBetween: 15,
        initialSlide: 1,
        speed: 400,
        grabCursor: true,
        // autoplay: {
        //     delay: 2500,
        //     disableOnInteraction: false,
        // }
    };

    theme: any;
    hometop_posts: any = [];
    homebottom_posts: any = [];
    ch_id: any;

    constructor(
        private sermonsService: SermonService,
        private storage: Storage,
        private route: ActivatedRoute,
        private idService: IdService,
        private loader: LoaderService,

    ) {
    }

    ngOnInit() {
        this.getTheme();

        this.ch_id = this.route.snapshot.paramMap.get('id');
        // setting latest church ID
        this.idService.setId(this.ch_id);

    }

    getTheme() {
        // getting theme from local storage
        this.storage.get('theme').then(theme => {
            if (theme) {
                this.theme = theme;
                this.getHomeTopPosts(this.theme.wp_site_apollo);
                this.getHomeBottomPosts(this.theme.wp_site_apollo);
            }
        });
    }

    getHomeTopPosts(url) {
        let self = this;
        // fetch notes by category
        this.sermonsService.getHomeTopPosts(url).subscribe((res: any) => {
            // ======= parsing xml into JsSON format =============================
            let parseString = require('xml2js').parseString;
            parseString(res, function (err, result) {
                self.hometop_posts = result.rss.channel[0].item;

                if (self.hometop_posts.length > 0){
                    self.hometop_posts.map( x => {
                       let attachment = x['content:encoded'][0];

                       if (attachment) {
                           let check = attachment.split('src="');
                           if (check[1]){
                               let check2 = check[1].split('"');
                               if (check2[0]){
                                   x['image'] = check2[0]
                               }
                           }
                       }
                    });

                }
            });
            // ======= parsing xml into JsSON format =============================
        });
    }

    getHomeBottomPosts(url) {
        let self = this;
        // fetch notes by category
        this.sermonsService.getHomeBottomPosts(url).subscribe((res: any) => {
            // ======= parsing xml into JsSON format =============================
            let parseString = require('xml2js').parseString;
            parseString(res, function (err, result) {
                self.homebottom_posts = result.rss.channel[0].item;

                if (self.homebottom_posts.length > 0){
                    self.homebottom_posts.map( x => {
                        let attachment = x['content:encoded'][0];
                        if (attachment) {
                            let check = attachment.split('src="');
                            if (check[1]){
                                let check2 = check[1].split('"');
                                if (check2[0]){
                                    x['image'] = check2[0]
                                }
                            }
                        }
                    });


                }

            });
            // ======= parsing xml into JsSON format =============================
        });
    }
}
